.App {
    text-align: center;
}

.start {
    opacity: 0;
    transform: scale(0);
}

.end {
    opacity: 1;
    transform: scale(1);
    transition: all 0.5s;
}

.slider {
    height: 500px;
    background-image: url('../public/img/slider.jpg');
    background-size: cover;
    background-position: center;
}

.c1 {
    text-decoration: none;
    color: #333;
    text-align: center;
}

.c1:hover {
    color: red;
}